.edit-business-info {
	align-items: end;
	flex: 1 1 auto;
	padding: 0px 30px;

	&-form-fields {
		display: flex;
		gap: 100px;
	}

	&-container {
		width: 100%;
		height: 100%;
		padding: 20px;
		margin: 0px 50px;
		display: flex;
		flex-direction: column;
		row-gap: 15px;

		h1 {
			text-align: center;
		}
	}
}

.logo-upload {
	flex: 0 0 15%;
}

.upload-box {
	border: 2px dashed gray;
	padding: 10px;
	text-align: center;
	height: 100px;
	font-size: 14px;
	color: #666;
}

.form-column {
	display: grid;
	gap: 20px;
	flex: 1 1 auto;
}

.save {
	&-section {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		justify-content: flex-end;
	}

	&-note {
		font-size: 12px;
		color: #777;
		margin-bottom: 10px;
	}
}
