/* Import Josefin Sans font family from Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap');

/* vars for easier down the road management */
:root {
	--turq: #008080;
	--check-turq: #007074;
	--dark-turq: #034c53;
	--mint-green: #3eb489;
	--salmon: #fa8072;
	--light-salmon: #ffb6ae;
	--delete-red: #dc3545;
	--light-pink: #ffe5e5;
	--gray: #7e7d7d;
	--gray-hover: #666666;
	--table-row-hover: #e3e3e3;
	--hard-black: #000000;
	--hard-white: #ffffff;
	--font-family: 'Josefin Sans', sans-serif;
	--corners-round: 5px;
	--border: 1px solid rgba(0, 0, 0, 0.25);
	--border-dashed: 1px dashed rgba(0, 0, 0, 0.25);
}

* {
	font-family: var(--font-family);
	box-sizing: border-box;
}

body {
	margin: 0px;
}

input {
	height: 30px;
	flex: 1 1 auto;
	border: 0px;

	&[type='text']:not(.password),
	&[type='number'],
	&[type='email'] {
		padding: 10px;
		border: 1px solid #ccc;
		border-radius: var(--corners-round);
		font-size: 14px;
	}

	&[type='checkbox'].checkbox:checked {
		background-color: var(--check-turq);
		border-radius: var(--corners-round);
		border: 1px solid black;
	}

	&[type='email'] {
		&.email {
			width: 100% !important;
		}
	}
}

label {
	font-size: 15px;
}

textarea {
	padding: 10px;
	border: 1px solid #ccc;
	border-radius: var(--corners-round);
	font-size: 14px;
}

#root {
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
}

.flex-container {
	display: flex;
	width: 100%;
}

.content {
	background-color: var(--hard-white);
	padding: 20px 25px;
	flex: 1 1 auto;
	display: flex;
}

.text {
	color: var(--hard-black);
	text-align: left;
	font-family: var(--font-family);
}

.dash-title {
	color: var(--dark-turq);
	text-align: center;
	font-size: 30px;
	font-weight: bold;
	font-family: var(--font-family);
}

.required {
	color: red;
}

/* BUTTONS */
/* general button */
.button {
	background-color: var(--mint-green);
	border: none;
	border-radius: var(--corners-round);
	padding: 10px 32px;
	margin: 5px;
	color: var(--hard-white);
	font-family: var(--font-family);
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	cursor: pointer;
}

.btn {
	background-color: var(--mint-green);
	border: none;
	border-radius: var(--corners-round);
	padding: 10px 20px;
	color: var(--hard-white);
	font-family: var(--font-family);
	text-align: center;
	text-decoration: none;
	display: flex;
	gap: 5px;
	align-items: center;
	font-size: 16px;
	cursor: pointer;
	position: absolute;
	left: 25px;

	img {
		width: 20px;
		height: 20px;
	}
}

.gray-btn {
	background-color: var(--gray);

	&:hover {
		background-color: var(--gray-hover);
	}
}

/* This section tentatively for Confirm Deletion and its parts. */
.confirm-delete-box {
	display: flex;
	background-color: var(--light-pink);
}

.delete {
	/* Throw the buttons in this container. */
	&-buttons-box {
		display: flex;
		flex-wrap: wrap;
	}

	/* button */
	&-cancel {
		flex: 25%;
		order: 1;
		background-color: var(--dark-turq);
	}

	/* invisible div to space .delete-confirm and .delete-cancel */
	&-spacer {
		flex: 50%;
		flex-direction: row;
		order: 2;
	}

	/* button */
	&-confirm {
		flex: 25%;
		order: 3;
		background-color: var(--delete-red);
	}
}

/* CHECKBOXES */
.unchecked {
	background-color: var(--check-box);
	border-radius: 5px;
	border: 1px solid black;
}

.checked {
	background-color: var(--check-turq);
	border-radius: var(--corners-round);
	border: 1px solid black;
}

/* CARDS */
.card {
	width: 250px;
	margin: 10px;
	text-align: center;

	&-title {
		text-align: center;
		font-size: 20px;
		text-decoration-line: underline;
	}

	&-text {
		font-size: 14px;
	}
}

/* MENU */
.menu {
	box-shadow:
		0 10px 20px 0 rgba(0, 0, 0, 0.2),
		0 10px 20px 0 rgba(0, 0, 0, 0.19);

	&-text {
		font-size: 30px;
		color: var(--hard-black);
	}

	&-items-container {
		padding: 30px;
	}

	&-header-row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 20px;
		gap: 100px;
	}

	&-name {
		text-align: center;
		font-size: 28px;
		font-weight: bold;
		color: var(--dark-turq);
		flex-grow: 1;
	}

	&-header-row > .button {
		&:first-child {
			margin-right: auto;
		}

		&:last-child {
			margin-left: auto;
		}
	}

	&-search {
		width: 40%;
		padding: 10px;
		border: 1px solid #ccc;
		border-radius: var(--corners-round);

		&-wrapper {
			display: flex;
			justify-content: center;
			margin-bottom: 20px;
		}
	}

	&-item {
		&-list {
			text-align: center;

			h2 {
				font-size: 24px;
				color: #034c53;
				margin-bottom: 20px;
			}
		}

		&-card {
			display: flex;
			flex-direction: column;
		}

		&-panel {
			width: 100%;

			.panel-header span {
				color: var(--gray);
				font-size: 1rem;
				flex: 1;
				text-align: left;
			}

			.panel-actions {
				display: flex;
				gap: 10px;
				justify-content: flex-end;
				align-items: center;

				.fa {
					&-trash {
						color: var(--delete-red);
						cursor: pointer;
					}

					&-pencil-alt {
						color: var(--hard-black);
						cursor: pointer;
					}
				}
			}
		}
	}
}

/* ALLERGEN */
.allergen {
	background-color: var(--salmon);

	&-add {
		border-style: solid;
		border-color: black;
		border-width: 1px;
		border-radius: 5px;
		padding: 10px;
	}

	&-tag {
		padding: 6px 12px;
		background-color: var(--salmon);
		color: var(--hard-white);
		border-radius: var(--corners-round);
		font-size: 14px;
		display: inline-flex;
		align-items: center;
		font-weight: bold;
	}

	&-tags-container {
		display: flex;
		flex-wrap: wrap;
		gap: 8px;
		margin: 10px 0;
		min-height: 40px;
		align-items: center;
	}

	&-label {
		padding: 6px 12px;
		background-color: var(--hard-white);
		border-radius: var(--corners-round);
		font-size: 14px;
		transition: all 0.2s;
	}
}

.left-side {
	float: left;
	padding: 15px;
}

.displayAllergens {
	align-items: center;
	height: 20px;
	width: 50%;
	margin: 5px;
	border-style: solid;
	border-color: #d9d9d9;
	border-width: 1px;
	border-radius: 5px;
}

.right-side {
	float: right;
	padding: 15px;
}

/* Panels */
.panel {
	&-header {
		cursor: pointer;
		padding: 10px;
		font-weight: bold;

		.angle-icon {
			margin-right: 10px;
			font-size: 1.2rem;
			color: var(--gray);
		}

		.header-title {
			flex: 1;
			text-align: left;
			font-size: 1rem;
		}
	}

	&-body {
		display: flex;
		padding: 10px;
	}

	&-footer {
		padding: 10px;
		display: flex;
		justify-content: space-between;
	}
}

.collapsible-panel {
	border: 1px solid #aeaeae;
	margin: 10px 0;
	border-radius: 4px;

	&-add {
		display: flex;
		flex-direction: column;
		border: 1px solid #aeaeae;
		margin: 10px 0;
		border-radius: 4px;
		flex-shrink: 0;
		overflow: hidden;
		height: auto;
		max-height: none;

		.panel-header {
			align-items: center;
			cursor: pointer;
			padding: 10px;
			font-weight: bold;
			background-color: var(--hard-white);
			border-bottom: 1px solid #d3d3d3;
		}
	}
}

.center {
	width: 100vw;
	display: flex;
	justify-content: center;
	box-sizing: border-box;
}

.add {
	&-header-row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 20px;
	}

	&-center-flex {
		flex-direction: column;
		align-items: center;
	}
}

.picklist-header-row {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 20px;
}

ul {
	list-style-type: none; /* Removes the bullet points */
	padding: 0; /* Removes default padding */
	margin: 0; /* Removes default margin */
}

/* Align the checkbox with the text */
.form-check {
	display: flex; /* Use flexbox for alignment */
	align-items: center; /* Vertically center the checkbox and text */
	gap: 8px; /* Add spacing between the checkbox and the text */

	&-input {
		margin: 0; /* Remove default margin from the checkbox */
	}
}

.form-field-container {
	display: grid;
	gap: 5px;
}

.confirmation,
.error {
	&-container {
		position: absolute;
		top: 30px;
		left: 50%;
		transform: translateX(-50%);
		padding: 10px 20px;
		border-radius: var(--corners-round);
		box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
		background-color: var(--hard-white);
		color: var(--hard-black);
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
	}
}

.confirmation {
	&-container {
		border: var(--border);
	}
}

.error {
	&-container {
		border: 2px solid var(--delete-red);
	}

	&-btn {
		background-color: var(--delete-red);
	}
}

.buttons {
	display: flex;
	justify-content: space-between;
	margin: 0px 30px;

	&:not(:has(.back-btn, .cancel-btn)) {
		justify-content: right;
	}

	.back-btn {
		position: relative;
	}
}

.set-up {
	&-container {
		padding: 0px 50px;
		display: flex;
		flex-direction: column;
		flex: 1 1 auto;
		row-gap: 20px;
	}
}

.set-up-container,
.step1,
.step2,
.step3 {
	flex: 1 1 auto;
	display: flex;
	flex-direction: column;

	h1 {
		text-align: center;
		margin-bottom: 15px;
	}
}

.choose-business,
.edit-login-info,
.step1,
.step2,
.step3 {
	&-form {
		margin: 0px 30px;
		padding: 20px;
		border: var(--border);
		border-radius: var(--corners-round);
		flex: 1 1 auto;
	}
}

.step1,
.step2 {
	&-form {
		display: grid;
		grid-template-columns: 1fr 1fr;
		column-gap: 20px;
	}
}

.edit-login-info,
.step3 {
	&-form {
		display: flex;
		flex-direction: column;

		.question {
			text-align: center;
			margin-bottom: 20px;
		}
	}
}

.question {
	font-size: 18px;
}

.allergen,
.diet,
.radio {
	&-label {
		display: flex;
		width: fit-content;
		align-items: center;
		column-gap: 5px;
		cursor: pointer;
	}
}
